@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,600);

* {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(131, 131, 131);
}

#root {
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.btn {
    outline: none;
    padding: 1rem 1.5rem;
    border-radius: 5px;
    border: none;
    color: white;
    background: #F05A5E;
    font-size: 1rem;
    margin: 1rem 0;
    cursor: pointer;
}
.startContainer {
    width: 100vw;
    padding: 0;
    position: absolute;
    background: rgb(238, 238, 238);
    min-height: 90vh;
    margin: 5vh 0;
    
    text-align: center;
}



.startContainer li {
    list-style: none;
    text-decoration: none;
}
.header {
    position: relative;
    width: 100%;
    background: white;
    color: black;
    margin: 0 auto;
    padding: 1rem;
    text-align: left;

}

.headerContainer {
    padding-top: 2rem;
    width: 70%;
    margin: 0 auto;
    text-align: center;
}

.brand {
    font-weight: 600;
    margin-top: 0;
}

.link {
    color: #F05A5E;
    font-family: 'Montserrat';
    text-align: left;
};



.paragraph {
    margin: 2rem 5rem;

}
.title {
    font-weight: 400;
}

.subtitle {
    color: grey;
}

#logo {
    height: 15%;
    width: 15%;
    min-width: 7rem;
    min-height: 7rem;
    max-width: 10rem;
    max-height: 10rem;;
}
.table {
    background: transparent;
    width: 100%;
    height: 100%;
    padding: 2rem 0;
}

.roomName {
    margin-bottom: 1rem;
}

.roomName a {
    -webkit-text-decoration-color: #F05A5E;
            text-decoration-color: #F05A5E;
}

.joinLink {
    margin-left: 2rem;
    text-decoration: none;
}
#text_tool {
    position: absolute;
    border: 1px dashed black;
    outline: 0;
    z-index: 1000 !important;
    display: none;
    overflow: hidden;
    white-space: nowrap;
}

.toolsContainer img {
    width: 2rem;
    height: 2rem;
    display: block;
    margin: 1.2rem auto;
    cursor: pointer;
    text-align: center;
}


.toolsContainer select {
    width: 80%;
    display: block;
    margin: 0.5rem auto 1rem auto;
    
}
.toolsContainer input {
    width: 80%;
    display: block;
    margin: 0.5rem auto 1rem auto;

}

span {
    color: rgb(55,55,55);
    text-align: center;

}





