.controlsContainer {
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 2;
    background: rgb(55, 55, 55);
    padding: 10% 0.5rem;
    text-align: center;
}

.controlsContainer ul {

    margin: 1rem 0;
    padding: 0;
    width: 100%;
    text-align: center;
}

.controlsContainer .control {
    list-style: none;
    display: block;
    margin: 1rem 0;
}

.controlsContainer .control:hover {
    cursor: pointer;
}

.controlsContainer .control img {
    width: 1.8rem;
    height: 1.8rem;
    margin: 0.5rem auto;
    display: block;
}

.controlsContainer .control span {
    font-size: 1rem;
    color: white;
}

.controlsContainer video {
    width: 95%;
    height: 90px;
    margin: 0.5rem auto;
    border: solid 1px white;
}


.modal {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    padding: 2rem;
    background: white;
    color: rgb(55, 55, 55);
    z-index: 1200;
    text-align: center;

}

.noShow {
    color: transparent;
}

.modal .inputTitle {
    margin: 1.8rem 0;
}

.modal input {
    font-family: 'Montserrat';
    width: 100%;
    height: 2rem;
    border: solid 1px #F05A5E;
    border-radius: 2px;
}

.modal .cancel {
    color: #F05A5E;
    background: white;
    box-sizing: border-box;
}

.modal .btn {
    border: solid 1.5px #F05A5E;
    margin: 0 1rem;
    padding: 0.6rem 1rem;
}
.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(55,55,55,0.6);
    z-index: 1000;
}

#board {
	/* overflow: hidden; */
	background: whitesmoke;
	width: 100%;
	height: 100%;
	text-align: left;
}

#canvas {
	cursor: crosshair;
	position: relative;
	background: white;
	overflow: scroll;
	margin: 0;
}
/* //////////////// TEXT ////////////////////*/
#textToolInput {
	position:fixed;
	top:-1000px; /*Hidden*/
	left:200px;
	width:100%;
}
#textToolInput:focus {
	top:0;
}

text {
	font-family:"Arial", "Helvetica", sans-serif;
	-webkit-user-select:none;
	    -ms-user-select:none;
	        user-select:none;
	-moz-user-select:none;
}

/* //////////////// RECTANGLE ////////////////////*/
rect {
	fill: none;
}

/* //////////////// PENCIL ////////////////////*/
path {
	fill: none;
	stroke-linecap: round;
	stroke-linejoin: round;
}
line {
	fill: none;
	stroke-linecap: round;
	stroke-linejoin: round;
}

.globalContainer {
    width: 100vw;
    height: 100vh;
    display: grid;
    /* grid-template-rows: 1; */
    grid-template-columns: 1fr 14fr 2fr;
    background: transparent;
}

.toolsContainer {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    background: white;
    border-right: solid 1px rgb(55, 55, 55);
    grid-row-start: 1;
    grid-row-end: 2;
    grid-column-start: 1;
    grid-column-end: 2;
    padding: 10% 0.5rem;
}

.controlsContainer {
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 2;
    padding: 10% 0.5rem;
}

#boardContainer {
    grid-row-start: 1;
    grid-row-end: 2;
    grid-column-start: 2;
    grid-column-end: 3;
    position: relative;
    background-color: transparent;
    position: relative;
    overflow: hidden;
}
